.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
	min-height: 100%;
}

h4{
  color: white;
  margin: 10px;
}

html{
  color-scheme: light;
}


.weatherContainer 
{
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.weatherContainer .searchContainer
{
  position: relative;
  display: inline;
  margin: 10px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.weatherContainer .searchContainer .searchInput {
  margin-left: 10px;
  height: 50px;
  border-radius: 10px;
  outline: none;
  border: none;
  width: 100%;
  
}

.weatherContainer .searchContainer .searchInput:focus{
  outline: 0px solid #dee1fc;
  border: none;
}

.weatherContainer .weatherBody{

  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  flex: 1;
  
}

.weatherContainer .weatherBody .weatherCardBody{
  box-shadow: 5px 5px 5px 2px lightgrey;
  border-radius: 5px;
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 45%;
  flex-wrap: wrap;
}

.weatherContainer .weatherBody .weatherCard{
  box-shadow: 0px 1px 5px 2px lightgrey;
  border-radius: 5px;
  height: 200px;
  width: 400px;
  margin: 10px;
  color: white;
  font-weight: 700;
  padding: 10px;
}

.weatherContainer .weatherBody .weatherCard .weatherCardHeader{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 25px;
  margin-top: 5px;
  margin-bottom: 5px;
 
}

.weatherContainer .weatherBody .weatherCard .weatherCardBodySubHeader{

   display: grid;
   grid-template-columns: .5fr .5fr;
   padding: 10px;
}

.weatherContainer .weatherBody .weatherCard .weatherCardBodySub{

  display: grid;
  align-items: left;
  grid-template-columns: 1fr 1fr;
  padding-top: 10px;
}

.weatherContainer .weatherBody .weatherFooter
{
  height: auto;
}


.weatherContainer .searchContainer .cityList{
  border: 1px solid #d9dce1;
  border-top: 0px;
  width: 100%;
  position: absolute;
}

.weatherContainer .searchContainer .cityList .cityListItem{
  border-bottom: 1px solid #b3baf3;
  height: 50px;
  padding-left: 10px;
  z-index: 99;
  display: flex;
  align-items: center;
  background-color:  #e6e8f8;
}

.cityListItem:hover{
  background-color: #f7f8fd;
  cursor: pointer;
}

.cityListItem:last-child{
  border-bottom: 0px;
}

.newsContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.box {
  width: 40%;
  height: 200px;
  border: 1px solid black;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  display: flex;
  flex-direction: row;
}

.thumbnail {
  width:40%;
  border-radius: 10px;
  margin:10px
}

.content {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  flex-wrap: wrap;
  
}

.title {
  font-weight: bold;
  height: 25%;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-top: 10px;
  margin-bottom: 10px;
}

.footer {
  font-size: 12px;
  margin-top: auto;
  display:flex;
  justify-content: space-between;
}

.categories{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.category-button
{
  border: 1px solid black;
  border-radius: 50px;
  margin: 10px;
  padding: 10px;
}

.category-button-clicked{
  background-color: #f8b4b4;
}

.category-button:hover{
  background-color: #f8b4b4;
  cursor: pointer ;
}


* {
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

.autocomplete-search-box {
  width: 50%;
  margin-left: 25%;
  box-shadow: 0 2px 10px 0px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  overflow: hidden;
}

.autocomplete-search-box .search-box {
  width: 100%;
  height: 40px;
  padding: 30px 20px;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 20px;
}

.autocomplete-search-box .search-result {
  width: 100%;
  padding: 0;
  margin: 0;
}

.autocomplete-search-box .search-result li {
  width: 100%;
  list-style-type: none;
  padding: 10px 20px;
}

.autocomplete-search-box .search-result li:hover {
  background: #3498db;
  color: white;
}
